// initial state
const state = {
  otherSpecial: [], // 

  scMan: null,
  zyMan: null,

  pipeBlockOperator0: [], // 0
  pipePullOperator0: [], // 0
  pipeBlockGuardian0: [], // 0
  pipePullGuardian0: [], // 0

  pipeBlockOperator1: [], // 1
  pipePullOperator1: [], // 1
  pipeBlockGuardian1: [], // 1
  pipePullGuardian1: [], // 1

  pipeBlockOperator2: [], // 2
  pipePullOperator2: [], // 2
  pipeBlockGuardian2: [], // 2
  pipePullGuardian2: [], // 2

  pipeBlockOperator3: [], // 3
  pipePullOperator3: [], // 3
  pipeBlockGuardian3: [], // 3
  pipePullGuardian3: [], // 3

  pipeBlockOperator4: [], // 4
  pipePullOperator4: [], // 4
  pipeBlockGuardian4: [], // 4
  pipePullGuardian4: [], // 4
};

// getters
const getters = {};

// actions
const actions = {
  changTag({ commit }, tags) {
    commit("setTag", { tags });
  },
  deleteTagItem({ commit, state }, tags) {
    let arr = state[tags.key];
    let newArr = arr.filter(item => {
      return item !== tags.value;
    });
    tags.value = newArr;
    commit("setTag", { tags });
  },
  cleanState({ commit }) {
    commit("resetState");
  }
};

// mutations
const mutations = {
  setTag(state, { tags }) {
    state[tags.key] = tags.value;
  },
  resetState(state) {
    for (const key in state) {
      if (state.hasOwnProperty(key)) {
        state[key] = [];
      }
    }
  }
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
};
