/**
 * axios
 * 
 */
import Vue from 'vue';
import axios from 'axios';
import router from '@/router';
import { Toast } from 'vant';

/**
 * 
 * 
 */
const tip = msg => {
    Toast({
        message: msg,
        duration: 1000,
        forbidClick: true
    });
};

/**
 * 
 * 
 */
const toLogin = () => {
    router.replace({
        path: '/login',
        query: {
            redirect: router.currentRoute.fullPath
        }
    });
};

/**
 * 
 * @param {Number} status 
 */
const errorHandle = (status, other) => {
    // 
    switch (status) {
        // 401: 
        case 401:
            toLogin();
            break;
        // 403 token
        // token
        case 403:
            tip('');
            localStorage.removeItem('token');
            setTimeout(() => {
                toLogin();
            }, 1000);
            break;
        // 404
        case 404:
            tip('');
            break;
        case 500:
            tip('');
            break;
        default:
            console.log(other);
    }
};

// axios
var instance = axios.create({ timeout: 1000 * 120 });
// post
instance.defaults.headers.post['Content-Type'] =
    'application/x-www-form-urlencoded';
/**
 * 
 * tokentoken
 */
instance.interceptors.request.use(
    config => {
        // token
        // tokentokentoken
        // token
        // 
        // const token = "store.state.token";
        // token && (config.headers.Authorization = token);
        return config;
    },
    error => Promise.error(error)
);

// 
instance.interceptors.response.use(
    // 
    res => {
        if (res.status === 200) {
            if (res.data.isValidCodeLogin === false) {
                localStorage.removeItem('JiaHuaSessionId');
                localStorage.removeItem('JiaHuaOfficeCode');
                localStorage.removeItem('JiaHuaUserName');
                localStorage.removeItem('JiaHuaOfficeName');
                Vue.prototype.$userInfo = null;
                tip('');
                setTimeout(() => {
                    toLogin();
                }, 1000);
                return Promise.reject(res.data);
            } else {
                return Promise.resolve(res.data);
            }
        } else {
            return Promise.reject(res.data);
        }
    },
    // 
    error => {
        const { response } = error;
        if (response) {
            // 2xx
            errorHandle(response.status, response.data.message);
            return Promise.reject(response);
        } else {
            // 
            // eg:statenetwork
            // networkapp.vue
            // 
            if (!window.navigator.onLine) {
                //    store.commit('changeNetwork', false);
            } else {
                return Promise.reject(error);
            }
        }
    }
);

export default instance;
